.login__btn {
  width: 300px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #1aab8a;
  color: #fff;
  border: none;
  position: relative;
  height: 60px;
  font-size: 18px;
  padding: 0 20px;
  cursor: pointer;
  transition: 800ms ease all;
  outline: none;
}
.login__btn:hover {
  background: #fff;
  color: #1aab8a;
}
.login__btn:before,
.login__btn:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 2px;
  width: 0;
  background: #1aab8a;
  transition: 400ms ease all;
}
.login__btn:after {
  right: inherit;
  top: inherit;
  left: 0;
  bottom: 0;
}
.login__btn:hover:before,
.login__btn:hover:after {
  width: 100%;
  transition: 800ms ease all;
}
